import Table from 'react-bootstrap/Table';
import { useDispatch, useSelector } from 'react-redux';
import { changeName, changeCount } from './../store.js';
import { memo, useState, useTransition } from 'react';

function Cart(){

  let state = useSelector((state)=>{return state}); // Redux store를 가져와줌
  let dispatch = useDispatch(); // store.js에 있는 reducers의 함수를 불러와줌

  return (
    <div>
      <div>{state.user.name +" "+state.user.age}</div>

      <Table striped bordered hover>
      <thead>
        <tr>
          <th>ID</th>
          <th>Title</th>
          <th>Count</th>
        </tr>
      </thead>
      <tbody>
        {
          state.cart.map((a, i)=>
            <tr>
              <td>{state.cart[i].id}</td>
              <td>{state.cart[i].title}</td>
              <td>{state.cart[i].count}</td>
              <td>
                <button onClick={()=>{
                  // dispatch(changeName())
                  dispatch(changeCount(state.cart[i].id))
                }}>+</button>
              </td>
            </tr>
          )
        }
      </tbody>
      </Table>
    </div>
  )
}

export default Cart;

function Item(props){
  return (
    <tr>
      <td>{props.id}</td>
      <td>{props.title}</td>
      <td>{props.count}</td>
    </tr>
  )
}